import React from "react";
import { LHS, RHS, FullWidthBtn } from "./general/style/common";
import { BottomTray, ProductCompare, ImgHolder } from "./general/style/style";

export default function ProductThumbnail({ itemSelected, onClick }) {
  // console.log("ProductView");
  return (
    <ProductCompare className="borderRight">
      <div className={itemSelected ? "singleProduct" : "singleProduct noContent"}>
        <div className="imgHolder">
          <img src={itemSelected ? itemSelected.image : ""} alt="" />
        </div>
        <p className="productName">{itemSelected ? itemSelected.name : ""}</p>
        <a
          href="javascript:void(0)"
          className="closeBtn"
          onClick={() => onClick(itemSelected ? itemSelected.id : null)}
        ></a>
      </div>
    </ProductCompare>
  );
}

